<template>
    <div class="card-admin">
        <div class="card-header" :class="header.bg">
        </div>
        <div class="title" :class="header.text">
            <slot name="title">
            </slot>
        </div>
        <div class="title-line"></div>
        <div class="content" :class="`${content.bg} ${content.text}`" >
            <slot name="content">
            </slot>
        </div>
    </div>
</template>

<script>
export default {
    name : 'cardItem',
    props : {
        headerBg : {
            type : String,
            default : 'black'
        },
        headerText : String,
        contentBg : String,
        contentText : String,
    },
    data() {
        return {
            listColor : ["danger","pink","success","orange",'primary',"white"],
            header  : {
                bg : 'bg-white',
                text : 'text-black',
            },
            content : {
                bg : 'bg-whte',
                text : 'text-black',
            }
        }
    },
    created() {
        // Set color element
        this.setHeaderColor();
        this.setContentColor();
    },
    methods : {
        setHeaderColor() {
            let headerBg = "";
            headerBg = this.listColor.find( color => color == this.headerBg );
            if(!headerBg) headerBg = 'black';
            this.header.bg = `bg-${headerBg}`;
            //  Set fontcolor
            let textColor = "";
            textColor = this.listColor.find( color => color == this.headerText );
            if(!textColor) textColor = 'black';
            this.header.text = `text-${textColor}`;
            return true;
        },
        setContentColor() {
            let contentBg = "";
            contentBg = this.listColor.find( color => color == this.contentBg);
            if(!contentBg) contentBg = "transparent";
            this.content.bg = `bg-${contentBg}`;
            // Set text color
            let textColor = "";
            textColor = this.listColor.find(color => color == this.contentText);
            if(!textColor) textColor = "black";
            this.content.text = `text-${textColor}`;

        }
    }
}
</script>

